.intro {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), transparent),
    url(../../backgroundImage/tech.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  padding: 4rem;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  cursor: default;
  text-align: center;
  z-index: 1;
  height: 100vh;
}

.arrow {
  font-size: 3rem;
  list-style: none;
  text-align: center;
}

#first-arrow {
  padding-top: 10rem;
}

.white-links {
  color: white;
}
.white-links :hover {
  color: #00587a;
}

.links {
  color: white;
  font-size: 2rem;
}

.card-text {
  color: black;
}
