@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto Slab', serif;
  background-image: linear-gradient(#99a8b2, #9ba4b4, #394867);
  color: white;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  scroll-behavior: smooth;
}

.profilePic {
  height: 200px;
  width: 150px;
  margin-right: 1rem;
}

hr {
  background-color: white;
}

.btn-primary {
  width: 10rem;
}

.typewriter h1 {
  overflow: hidden;
  white-space: nowrap;
}

.typewriter h1 {
  animation: animated-text 2s steps(50, end) 0.5s 1 normal both;
}

/* The typing effect */
@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 320px;
  }
}

.typewriter p,
.arrow {
  white-space: nowrap;
  animation: display-content 2s ease-in 2s normal both;
}

@keyframes display-content {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.card img {
  height: 10em;
}

#last-arrow {
  margin-bottom: -15em;
}

.btn-div {
  position: absolute;
  bottom: 0;
  left: 25%;
  right: 25%;
}

.card-text {
  position: relative;
  min-height: 25em;
}

.skills,
li {
  font-size: 1em;
  list-style: none;
}
